import request from '@/https'

export function getCover(params) {
    return request({
        url: '/cover/getAllCover',
        method: 'get',
        params
    })
}

export function sendPdf(params) {
    return request({
        url: '/pDFMergeController/mergerPdf',
        method: 'post',
        params
    })
}

export function addDownloadNum(params) {
    return request({
        url: '/thirdLevel/addDownloadNum',
        method: 'get',
        params
    })
}



